<template>
	<div>
		<div class="goods">
			<div class="menu-wrapper" ref="menuWrapper">
				<ul>
					<li class="menu-item" v-for="(item, index) in goods" :class="{'current': currenteIndex === index}" 
					@click="selectMenu(index, $event)">
						<span class="text">
							<span v-show="item.type>0" class="icon" :class="classMap[item.type]"></span>
							{{ item.name }}
						</span>
					</li>
				</ul>
			</div>
			<div class="foods-wrapper" ref="foodsWrapper">
				<ul>
					<li class="food-list food-list-hook" v-for="item in goods">
						<h1 class="title">{{ item.name }}</h1>
						<ul>
							<li @click="selectFood(food, $event)" class="food-item" v-for="food in item.foods">
								<div class="icon">
									<img width="57" height="57" :src="food.icon">
								</div>
								<div class="content">
									<h2 class="name">{{ food.name }}</h2>
									<p class="desc">{{ food.description }}</p>
									<div class="extra">
										<span class="count">月售{{ food.sellCount }}份</span><span>好评率{{ food.rating }}%</span>
									</div>
									<div class="price">
										<span class="now">￥{{ food.price }}</span>
										<span class="old" v-show="food.oldPrice">￥{{ food.oldPrice }}</span>
									</div>
									<div class="cartcontrol-wrapper">
										<cartcontrol :food="food"></cartcontrol>
									</div>
								</div>
							</li>
						</ul>
					</li>
				</ul>
			</div>
			<shopcart ref="shopcart" :select-foods="selectFoods" :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice"></shopcart>
		</div>
		<food :food="selectedFood" ref="food"></food>
	</div>
</template>

<script>
	import BScroll from 'better-scroll'
	import shopcart from 'components/shopcart/shopcart.vue'
	import cartcontrol from 'components/cartcontrol/cartcontrol.vue'
	import food from 'components/food/food.vue'

	const ERR_OK = 0

	export default {
		props: {
			seller: {
				type: Object
			}
		},
		computed: {
			currenteIndex () {
				for (let i = 0; i < this.listHeight.length; i++) {
					let height1 = this.listHeight[i]
					let height2 = this.listHeight[i + 1]
					if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
						// console.log('foodIndex: ' + i)
						// console.log('height1: ' + height1)
						// console.log('height2: ' + height2)
						// console.log('this.scrollY: ' + this.scrollY)
						return i
					}
				}
				return 0
			},
			selectFoods () {
				let foods = []
				this.goods.forEach((good) => {
					good.foods.forEach((food) => {
						if (food.count) {
							foods.push(food)
						}
					})
				})
				return foods
			}
		},
		data () {
			return {
				goods: [],
				listHeight: [],
				scrollY: 0,
				selectedFood: {}
			}
		},
		created () {
			this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']
			this.$http.get('/api/goods').then((response) => {
				response = response.body
				if (response.errno === ERR_OK) {
					this.goods = response.data

					this.$nextTick(() => {
						this._initScroll()
						this._calculate()
						// console.log(this.listHeight)
					})
				}
			})
            this.$root.eventHub.$on('cart.add', (target) => {
				this._drop(target)
			})
		},
		methods: {
			selectFood (food, event) {
				if (!event._constructed) {
					return
				}
				this.selectedFood = food
				this.$refs.food.show()
			},
			selectMenu (index, event) {
				if (!event._constructed) {
					return
				}
				let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook')
				let el = foodList[index]
				this.foodsScroll.scrollToElement(el, 300)
				// console.log('menuIndex: ' + index)
			},
			_drop (target) {
				// 体验优化，异步执行下落动画
				this.$nextTick(() => {
					this.$refs.shopcart.drop(target)
				})
			},
			_initScroll () {
				this.menuScroll = new BScroll(this.$refs.menuWrapper, {
					click: true
				})

				this.foodsScroll = new BScroll(this.$refs.foodsWrapper, {
					click: true,
					probeType: 3
				})

				this.foodsScroll.on('scroll', (pos) => {
					// console.log('pos.y: ' + pos.y)
					this.scrollY = Math.abs(Math.round(pos.y))
					// console.log('this.scrollY: ' + this.scrollY)
				})
			},
			_calculate () {
				let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook')
				let height = 0
				this.listHeight.push(height)
				for (let i = 0; i < foodList.length; i++) {
					let item = foodList[i]
					// console.log('item.offsetHeight: ' + item.offsetHeight + '---item.clientHeight: ' + item.clientHeight)
					height += item.clientHeight
					this.listHeight.push(height)
				}
			}
		},
		components: {
			shopcart,
			cartcontrol,
			food
		}
	}
</script>

<style lang="stylus">
	@import '../../common/stylus/mixin.styl'

	.goods
		display: flex
		position: absolute
		top: 174px
		bottom: 48px
		width: 100%
		overflow: hidden
		.menu-wrapper
			flex: 0 0 80px
			width: 80px
			background: #f3f5f7
			.menu-item
				display: table
				height: 54px
				width: 56px
				line-height: 14px
				padding: 0 12px
				&.current
					position: relative
					z-index: 10
					margin-top: -1px
					background: #fff
					.text
						border-none()
						font-weight: 700
				.icon
					display: inline-block
					vertical-align: top
					width: 12px 
					height: 12px
					margin-right: 2px
					background-size: 12px 12px
					&.decrease
						bg-image('decrease_3')
					&.discount
						bg-image('discount_3')
					&.guarantee
						bg-image('guarantee_3')
					&.invoice
						bg-image('invoice_3')
					&.special
						bg-image('special_3')
				.text
					display: table-cell
					width: 56px
					line-height: 14px
					font-size: 12px
					font-weight: 200
					vertical-align: middle
					border-1px(rgba(7, 17, 27, 0.1))
		.foods-wrapper
			flex: 1
			.title
				height: 26px
				line-height: 26px
				padding-left: 14px
				font-size: 12px
				border-left: 2px solid #d9dde1
				color: rgb(147, 153, 159)
				background: #f3f5f7
			.food-item
				display: flex
				margin: 18px
				padding-bottom: 18px
				border-1px(rgba(7, 17, 27, 0.1))
				&:last-child
					border-none()
					margin-bottom: 0
				.icon
					flex: 0 0 57px
					margin-right: 10px
				.content
					flex: 1
					.name
						font-size: 14px
						line-height: 14px
						margin: 2px 0 8px 0
						color: rgb(7, 17, 27)
					.desc, .extra
						font-size: 10px
						line-height: 10px
						color: rgb(147, 153, 159)
					.desc
						line-height: 12px
						margin-bottom: 8px
					.extra
						.count
							margin-right: 12px
					.price
						font-weight: 700
						font-size: 0
						line-height: 24px
						.now
							display: inline-block
							vertical-align: top
							margin-right: 8px
							font-size: 14px
							color: rgb(240, 20, 20)
						.old
							display: inline-block
							text-decoration: line-through
							font-size: 10px
							color: rgb(147, 153, 159)
					.cartcontrol-wrapper
						position: absolute
						right: 0
						bottom: 12px
</style>



