<template>
	<div class="header">
		<div class="background">
			<img :src="seller.avatar" width="100%">
		</div>
		<div class="content-wrapper">
			<div class="avatar">
				<img width="64" height="64" :src="seller.avatar">
			</div>
			<div class="content">
				<div class="title">
					<span class="brand"></span>
					<span class="name">{{ seller.name }}</span>
				</div>
				<div class="description">
					{{ seller.description }}/{{ seller.deliveryTime }}分钟送达
				</div>
				<div class="support" v-if="seller.supports">
					<span class="icon" :class="classMap[seller.supports[0].type]"></span>
					<span class="text">{{ seller.supports[0].description }}</span>
				</div>
			</div>
			<div class="support-count" v-if="seller.supports" @click="showDetail">
				<span class="count">{{ seller.supports.length }}个</span>
				<i class="icon-keyboard_arrow_right"></i>
			</div>
		</div>
		<div class="bulletin-wrapper" @click="showDetail">
			<span class="bulletin-title"></span><span 
			class="bulletin-text">{{ seller.bulletin }}</span>
			<i class="icon-keyboard_arrow_right"></i>
		</div>
		<transition name="fade">
			<div class="detail" v-show="detailShow">
				<div class="detail-wrapper clearfix">
					<div class="detail-main">
						<h1 class="name">{{ seller.name }}</h1>
						<div class="star-wrapper">
							<star :size="48" :score="seller.score"></star>
						</div>
						<div class="title">
							<div class="line"></div>
							<div class="text">优惠信息</div>
							<div class="line"></div>
						</div>
						<ul class="supports" v-if="seller.supports">
							<li class="support-item" v-for="(item, index) in seller.supports">
								<span class="icon" :class="classMap[seller.supports[index].type]"></span>
								<span class="text">{{ seller.supports[index].description }}</span>
							</li>
						</ul>
						<div class="title">
							<div class="line"></div>
							<div class="text">商家公告</div>
							<div class="line"></div>
						</div>
						<div class="bulletin">
							<p class="content">{{ seller.bulletin }}</p>
						</div>
					</div>
				</div>
				<div class="detail-close" @click="hideDetail">
					<i class="icon-close"></i>
				</div>
			</div>
		</transition>
	</div>
</template>

<script>
	import star from 'components/star/star.vue'

	export default {
		props: {
			seller: {
				type: Object
			}
		},
		data () {
			return {
				detailShow: false
			}
		},
		methods: {
			showDetail () {
				this.detailShow = true
			},
			hideDetail () {
				this.detailShow = false
			}
		},
		created () {
			this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']
		},
		components: {
			star
		}
	}
</script>

<style lang="stylus">
	@import "../../common/stylus/mixin.styl"

	.header
		position: relative
		overflow: hidden
		color: #fff
		background: rgba(7, 17, 27, 0.5)
		.background
			z-index: -1
			position: absolute
			left: 0
			top: 0
			width: 100%
			height: 100%
			filter: blur(10px)
		.content-wrapper
			position: relative
			padding: 24px 12px 18px 24px
			font-size: 0
			.avatar
				display: inline-block
				vertical-align: top
				img
					border-radius: 2px
			.content
				display: inline-block
				margin-left: 16px
				.title
					margin: 2px 0 8px 0
					.brand
						display: inline-block
						vertical-align: top
						width: 30px
						height: 18px
						bg-image('brand')
						background-size: 30px 18px
						background-repeat: no-repeat
					.name
						margin-left: 6px
						font-size: 16px
						line-height: 18px
						font-weight: bold
				.description
					font-size: 12px
					line-height: 12px
					margin-bottom: 10px
				.support
					margin-bottom: 2px
					.icon
						display: inline-block
						vertical-align: top
						width: 12px 
						height: 12px
						margin-right: 4px
						background-size: 12px 12px
						&.decrease
							bg-image('decrease_1')
						&.discount
							bg-image('discount_1')
						&.guarantee
							bg-image('guarantee_1')
						&.invoice
							bg-image('invoice_1')
						&.special
							bg-image('special_1')
					.text
						line-height: 12px
						font-size: 10px
			.support-count
				position: absolute
				right: 12px
				bottom: 14px
				height: 24px
				line-height: 24px
				padding: 0px 8px
				border-radius: 14px
				text-align: center
				background-color: rgba(0, 0, 0, 0.2)
				.count
					vertical-align: top
					font-size: 12px
				.icon-keyboard_arrow_right
					margin-left: 2px
					font-size: 10px
					line-height: 24px
		.bulletin-wrapper
			position: relative
			height: 28px
			line-height: 28px
			padding: 0 14px
			background-color: rgba(7, 17, 27, 0.2)
			white-space: nowrap
			overflow: hidden
			text-overflow: ellipsis
			.bulletin-title
				display: inline-block
				vertical-align: top
				margin-top: 8px
				width: 22px
				height: 12px
				bg-image('bulletin')
				background-size: 22px 12px
				background-repeat: no-repeat
			.bulletin-text
				vertical-align: top
				font-size: 10px
				margin-left: 4px
			.icon-keyboard_arrow_right
				position: absolute
				right: 8px
				top: 8px
				font-size: 12px
		.detail
			position: fixed
			left: 0
			top: 0
			z-index: 100
			width: 100%
			height: 100%
			overflow: auto
			background: rgba(7, 17, 27, 0.8)
			opacity: 1
			backdrop-filter: blur(10px)
			&.fade-enter-active,&.fade-leave-active
				transition: all 0.5s
			&.fade-enter,&.fade-leave-active
				opacity: 0
				background: rgba(7, 17, 27, 0)
			.detail-wrapper
				min-height: 100%
				width: 100%
				.detail-main
					margin-top: 64px
					padding-bottom: 64px
					.name
						line-height: 16px
						text-align: center
						font-size: 16px
						font-weight: 700
					.star-wrapper
						margin-top: 16px
						padding: 2px 0
						text-align: center
					.title
						display: flex
						width: 80%
						margin: 28px auto 24px auto
						.line
							flex: 1
							position: relative
							top: -6px
							border-bottom: 1px solid rgba(255, 255, 255, 0.2)
						.text
							padding: 0 12px
							font-size: 14px
							font-weight: 700
							line-height: 14px
					.supports
						width: 80%
						margin: 0 auto
						.support-item
							padding: 0 12px
							margin-bottom: 12px
							font-size: 0
							&:last-child
								margin-bottom: 0
							.icon
								display: inline-block
								width: 16px
								height: 16px
								vertical-align: top
								margin-right: 6px
								background-size: 16px 16px
								backgorund-repeat: no-repeat
								&.decrease
									bg-image('decrease_2')
								&.discount
									bg-image('discount_2')
								&.guarantee
									bg-image('guarantee_2')
								&.invoice
									bg-image('invoice_2')
								&.special
									bg-image('special_2')
							.text
								font-size: 12px
								line-height: 16px
								font-weight: 200
					.bulletin
						width: 80%
						margin: 0 auto
						.content
							font-size: 12px
							line-height: 24px
							padding: 0 12px
			.detail-close
				position: relative
				width: 32px
				height: 32px
				margin: -64px auto 0 auto
				font-size: 32px
				color: rgba(255, 255, 255, 0.5)
</style>



